#task-summary {
  .task-summary-title {
    display: flex;

    img {
      width: 40px;
      height: 40px;
      padding: 5px;
    }

    h2 {
      width: fit-content;
      padding: 3px 6px;
      color: $default-title-color;
    }
  }
  .task-summary-container {
    border-color: $default-bg-color-5;

    .task-summary-columns {
      padding-bottom: 20px;

      .task-summary-column {
        color: inherit;
      }

      li {
        padding-bottom: 5px;
      }

      span {
        color: $default-title-color;
      }
    }

    .task-tags {
      margin-top: 0;

      li {
        border-style: none;
        margin: 0 6px 6px 0;
        padding: 3px 6px;
      }
    }
  }
}
